.ember-form .relationship-type-wrapper {
	margin-bottom: 0 !important;
	&.form-field.bulk {
		margin: 0 8px;
		height: 32px;
		float: left;

		.ember-power-select-trigger {
			margin: 0;
			border: 0;
			padding: 0;

			.bulk-add-trigger {
				margin: 0;
				padding: 4px 16px;
			}
		}

		.ember-basic-dropdown-content {
			right: auto;
			left: 0;
		}
	}

	.ember-power-select-trigger, .ember-power-select-selected-item { overflow: visible; }
	.ember-tooltip { padding: 4px; }

	.ember-basic-dropdown {
		.ember-basic-dropdown-content {
			width: 368px;
			right: 0;
			left: auto;

			>.ember-power-select-options {
				padding: 0;
			}

			// fixed header
			.stream-names-wrapper {
				display: flex;

				.stream-names {
					width: 50%;
					background: $background-color;
					color: $app-form-label-color;
					font-size: $text-normal;
					font-weight: $text-weight-semi-bold;    
					line-height: 32px;
					padding: 0 16px;
				}

			}

			.ember-power-select-group {
				display: inline-block;
				width: 50%;
				box-sizing: border-box;

				// hide default group names
				.ember-power-select-group-name {
					display: none;
				}

				.ember-power-select-options {
					&:nth-child(2) {
						border-right: 1px solid $border-light-color;
					}

					.ember-power-select-option {
						padding: 4px 8px;
						margin: 6px 8px;
						font-weight: $text-weight-normal;
					}

				}
			}
			.ember-power-select-search {
				padding: 8px;

				.search-container {
					left: 16px;
				}
		
				.ember-power-select-search-input {
					padding-left: 26px;
				}
			}
		}
	}
}