.sam {
	width:100%;

	.page-content{
		width: 100%;
		margin: 0;
	}

	
	svg.plus-circle-green {
		circle {
			fill: $app-jade-light;
			stroke: $app-green;
		}
		path {
			fill: $app-green !important;
		}
	}

	.user-views-dd{
		display: inline-block;
		cursor: pointer;
		font-weight: $text-weight-semi-bold;
		margin-bottom: 8px;
		.badge{
			background: $background-color;
			border-radius: 8px;
			margin: 0 8px;
			border: hidden;
			color: $secondary-text-color-1;
		}
		li{
			width: 150px;
		}
	}

	.saas-actions-dd{
		display: inline-block;
		font-weight: $text-weight-semi-bold;
		margin-bottom: 8px;
		margin-right: auto;
		li{
			width: 150px;
		}
	}
	.content-wrap {
		background-color: #FFFFFF;

		h4 {
			font-size: 18px;
			font-weight: 600;
			line-height: 1.5;
		}
	}

	.enable-insight-container {
		height: 30px;
		margin-right: 16px;
		display: flex;
		align-items: center;

		label {
			padding: 0;
			font-weight: $text-weight-medium;
		}

		.toggle-switch {
			margin-right: 8px;
		}
	}

	#detail-view-banner{
		background-color: $app-white;
		width: 100%;
		.text-wrapper{
			padding: 16px 32px 4px;
		}
	}
	#licenses-tab, #installations-tab, #relationships-tab {
		padding: 16px 32px;
		height: calc(100vh - 142px - 43px - 56px);
	}

	#overview-tab {
		.left-pane {
			.content-wrap {
				width: 100%;
			}
		}
	}

	.sam-details-table-wrap {
		&.sam-users-table {
			padding-right: 32px;
		}
		.sam-table-options {
			@include flexbox-simple(flex-end, center);
			padding: unset;
			.delete-bulk-user-btn, .delete-bulk-installation-btn, .view-more-user-opts {
				margin-right: auto;
			}
		}
	}

	#users-tab {
		height: calc(100vh - 142px - 43px - 56px);
		padding: 24px 0 0 32px;
		overflow: auto;
		.sam-users-table-wrap{
			display: flex;
			.sam-details-table-wrap {
				.sam-table-options {
					@include flexbox-simple(flex-end, center);
					padding: unset;
				}
			}
			.sam-users-table {
				width: 100%;
				.ember-light-table {
					border: 1px solid $app-border-primary;
					thead th.lt-column {
						position: static;
					}
				}
				&.sidebar-open {
					width:calc(100% - 300px) !important;
				}
			}
			.sam-users-sidebar{
				width: 300px;
				height: calc(100vh - 241px);
				margin-top: -24px;
				background: $app-sidebar-bg;
				border-left: 1px solid $app-border-primary;
				position: sticky;
				top: -24px;
				.filter-header {
					background: unset;
					box-shadow: unset;
				}
				.filter-row {
					.ember-basic-dropdown .ember-power-select-trigger {
						border: 1px solid $app-border-primary;
					}
					.ember-power-select-status-icon {
						right: 12px;
					}
				}
				.btns-wrapper {
					background: $background-color-2;
					box-shadow: unset;
				}
			}
		}
	}

	/* Phase 3 layout change Style */
	.name-wrap {
		display: flex;
		width: 65%;
		padding: 24px 10px 24px 2px;
		border-right: 1px solid $app-secondary-btn-hover;
		.first-content{
			width: 100%;
		}
		&.col{
			border:hidden;
		}
		.description{
			padding-right: 32px;
			word-break: break-all;
			white-space: pre-line;
			margin-bottom: 8px;
		}
		.view-more-less-desc {
			font-weight: $text-weight-semi-bold;
			white-space: nowrap;
		}
	}

	.actions-wrap {
		@include flexbox-simple(flex-start, center);
		align-self: flex-start;
		padding-top: 24px;
		flex-wrap: wrap;
		margin-left: auto;
		@media (min-width: 768px) { 
			flex-shrink: 0;
		}
	}

	.chart-visuals-wrap{
		@include flexbox-simple(flex-start, stretch);
		.left-pane {
			display: flex;
			width: 73%;
			padding: 24px 32px 24px 32px;
			box-shadow: inset 0 4px 8px $dropdown-search-shadow-color;
		}
		.right-pane {
			padding: 16px 0 0 32px;
			width: 27%;
			border-left: 1px solid $border-dark-color;
			box-shadow: inset 0px 4px 8px $dropdown-search-shadow-color;
		}
	}

	.wrap-text{
		word-break: break-all;
		white-space: pre-line;
	}

	.sam-basic-props {
		display: flex;
		padding: 0 32px;
		position: relative;
		background-color: $app-light-bg;
		.sw-category{
			margin-bottom: 0;
		}
		.software-logo{
			width: 44px;
			margin-right: 24px;
			padding-top: 5px;
			.logo-img{
				width: 42px;
				height: 42px;
				border: 1px solid $border-light-color-2;
				border-radius: 4px;
				text-align: center;

				img{
					max-width: 100%;
					height: 100%;
					padding: 4px;
					box-sizing: border-box;
				}
			}
		}
	}

	.status-wrap {
		width: 15%;
		padding: 24px 0;
		.separator {
			padding: 0 10px;
		}
	}

	.sw-category{
		font-size: $text-normal;
		color: $app-form-label-color;
		span:not(.property_title){
			font-weight: $text-weight-medium;
			padding: 0 5px;
			word-break: break-all;
			&.sw-status{
				text-transform: capitalize;
			}
		}
	}

	/* Phase 3 layout change Style */

	.properties-col-title{
		color: $app-icon-color;
		font-weight: $text-weight-semi-bold;
		margin-bottom: 24px;
	}
	.property_title {
		font-size: $text-normal;
		color: $app-form-label-color;
		word-break: break-word;
		margin-bottom: 4px;
	}

	.properties_wrapper {
		&.status-wrap .content-row {
			overflow: visible;
		}

		.content-row {
			a {
				color: $app-grey;

				&:hover {
					color: $brand-theme-color;
				}
			}
			overflow: hidden;
			.field-content {
				padding : 0 16px 24px 0;

				&.software-source {
					span {
						font-weight: normal;
						font-size: 10px;
						margin: 5px 5px 0 0;
					}
				}
				&.paragraph {

					.property_value span{
						font-weight:400;
						white-space: pre-line;
					}
				}
				.property_value {
					color: $app-icon-color;
					font-weight: 600;
					overflow-wrap: break-word;

					.user-prop {
						display: flex;
						align-items: center;
					}
					.user-avatar img {
						width: 30px;
						height: 30px;
						border-radius: 100%;
					}
					.user-name {
						padding-left: 4px;
					}
					.user-email{
						font-size: 12px;
						color: $linkColor;
						font-weight: $text-weight-normal;
					}
				}
			}
		}
	}

	.grid-container {
		display: grid;
		grid-template-columns: auto auto auto;

		&.grid-2-container {
			grid-template-columns: 30% auto;
		}
	}

	.software-summary-header {
		border-bottom: 1px solid #EBEFF3;
		padding: 12px 16px;
		overflow: hidden;
		display: flex;
		align-items: center;
		.software-summary-status {
			margin-left: auto;
			padding-top: 4px;
			.value {
				font-weight: $text-weight-semi-bold;
				text-transform: capitalize;
			}
		}
	}

	.software-summary-visuals {
		position: relative;
		width: inherit;
		min-height: 230px;
		display: flex;
		flex-wrap: wrap;

		// allocation and license overview section
		.allocation-and-license-overview-container {
			width: 100%;
			box-sizing: border-box;
			overflow: hidden;
			position: absolute;
			display: flex;
			right: 0;
			bottom: 0;
			height: 55px;
			padding: 16px;
			background: $app-casablanca-light;
			font-weight: $text-weight-semi-bold;
			.text {
				float: left;
				line-height: 16px;
			}
			.icon {
				float: left;
				margin-right: 8px;
				position: static;
				svg {
					width: 16px;
					height: 16px;
				}
			}
		}
		.summary-sections:nth-child(odd) {
			margin-right: 2%;
		}
		.summary-sections {
			width: 49%;
			min-width: 300px;
			overflow: hidden;
			position: relative;
			box-sizing: border-box;
			min-height: 300px;
			margin-bottom:24px;
			border: 1px solid #EBEFF3;
			box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.05);
			border-radius: 8px;


			.highcharts-container {
				margin: auto;

				.usage-tooltip {
					color: $app-primary;
					font-weight: $text-weight-semi-bold;
				}
			}

			.empty-note {
				position: absolute;
				text-align: center;
				width: 100%;
				bottom: 32px;
				font-size: $text-normal;
				color: $mild-text;
			}


			&.app-data-visualisation {

				.chart-header-container {
					padding: 16px 18px 0 24px;
					align-items: center;
					.chart-header {
						padding: 0;
					}
				}

				.chart-content-container {
					padding: 0 16px 4px 16px;
					min-height: 258px;
				}

				.chart-footer-container {
					padding: 4px 24px 12px;
					text-align: right;
					display: block;

					.last-sync-date {
						display: inline-flex;
						align-items: center;
						justify-content: space-around;
						background: $highlight-grey;
						cursor: default;
						padding: 2px 12px;
						font-size: $text-normal;

						.renew-icon {
							width: 14px;
							height: 30px;
							margin-right: 8px;
						}
					}
				}

				.chart-content-container,.chart-header-container {
					display: flex;
					justify-content: space-between;
				}

				&.occupyContainer {
					width: 100%;
					margin-right: 0;

					.left-col,.right-col {
						width: 50%;
					}

					.right-col {
						border-left: 1px solid $border-light-color ;
						padding-left: 32px;
					}

					.left-col {
						padding-right: 32px;
					}
				}

				.left-col, .right-col {
					width: 100%;
					box-sizing: border-box;
					padding: 24px 0 12px 0;
				}

			}

			// allocation section
			&.allocation-overview-container {
				.info-icon {
					position: relative;
					left: 4px;
					top: 4px;
					svg {
						fill: $app-primary;
					}
				}
				.legend-details .label-container {
					width: 8vw;
				}
			}

			// license overview section
			&.license-overview-container {
				.content {
					padding: 32px 0 0 16px;

					>div {
						overflow: hidden;
						div {
							float: left;
							line-height: 32px;
						}
						.value {
							font-weight: $text-weight-semi-bold;
							.note {
								float: none;
								color: $secondary-text-color-1;
								line-height: 14px;
							}
						}
						.label-container {
							width: 228px;
							>div {
								margin: 0 8px 16px 0;
								&:not(.text) {
									padding: 8px;
									border-radius: 16px;
									line-height: 8px;
								}
								&.expiry-date-icon {
									background: $app-jade-light;
								}
								&.total-value-icon {
									background: $dropdown-option-selected;
								}
								&.disabled {
									background: $dropdown-hover;
									path,use {
										fill: $disabled-text-color;
									}
								}
							}
							svg {
								height: 16px;
								width: 16px;
							}
						}
					}
				}
			}
			// usage section
			&.usage-overview-container {
				.header_wrapper {
					padding: 16px 16px 0 16px;
					overflow: hidden;
					h1 {
						padding: 0;
						float: left;
					}
					span {
						float: right;
						color: $secondary-text-color-1;
					}
				}
			}
			.empty-content {
				margin: 64px 0 0 24px;
				>div {
					float: left;
				}
				.empty-usage-img {
					img {
						width: 120px;
						height: 120px;
						position: relative;
						bottom: 16px;
					}
				}
				.coming-soon-wrapper {
					width: 17vw;
					padding-left: 24px;
					.note {
						color: $secondary-text-color-1;
						padding-top: 12px;
						h2 {
							font-weight: bold;
							padding-bottom: 8px;
							font-size: $title-small;
						}
					}
				}
			}

			&:not(:last-child) {
				border-right: solid 1px $border-light-color;
			}

			h1 {
				font-weight: bold;
				padding: 16px 0 0 16px;
				font-size: $title-small;
				line-height: 20px;
			}

			.donut-chart-wrapper {
				padding: 16px 3vw 0 0;
				width: 162px;
				float: left;
				box-sizing: content-box;
			}
			// common css for legends
			.legend-details {
				float: left;
				padding-top: 56px;
				.legends {
					padding-bottom: 16px;
					//dark diabled icon for first legend
					&:nth-child(2) {
						.disabled {
							color: $disabled-text-color !important;
						}
					}
					//light diabled icon for second legend
					&:nth-child(3) {
						.disabled {
							color: $inputBorder !important;
						}
					}
				}
				.label-container {
					color: $inputHoverBorder;
					width: 10vw;
					display: inline-block;
					.chart-legend-bullet {
						padding-right: 4px;
						//legend colours
						.allocated {
							color: $app-primary;

						}

						.available,.active {
							color :$app-green;

						}

						.inactive {
							color: $app-red;
						}

					}
				}
				.value {
					font-weight: $text-weight-semi-bold;
				}
			}
		}
	}

	.app-data-visualisation {
		.ember-modal-header {
			.title h2 {
				font-weight: $text-weight-semi-bold;
			}
		}
		.ember-modal-body {
			height: calc(100vh - 69px);
			.chart-content-container {
				margin: 16px -12px 0 -10px;
			}
		}
	}

	.software-details-tabs {
		padding: 0;
		margin: 0;
		// This code should comes under the tab component
		.tabs-badge{
			background: $app-grey-bg-2;
			padding: 1px 6px;
			border-radius: 4px;
			color: $secondary-text-color-1;
			font-size: 12px;
		}
		.tabs__items {
			height: 44px;
			padding: 0 32px;
			&.have-soft-img {
				padding-left: 100px;
			}
			.tabs__item__nav{
				line-height: 41px;
				margin: 0 40px 0 0;
				outline: 0;
				padding: 0;
				border-bottom: 1px solid $border-dark-color;
				padding-top: 2px;
				.tab-title {
					font-weight: $text-weight-semi-bold;
				}
			}
			border-bottom: 1px solid $border-dark-color;
			box-shadow: 0 4px 8px $dropdown-search-shadow-color;
			a {
				&.active {
					background: transparent;
					.count_wrapper {
						display: inline;
					}
				}
				.count_wrapper {
					display: none;
				}
			}
		}
		.tabs__content__pane {
			padding: 0;
		}
	}

	#relationships-tab {
		height: calc(100vh - 142px - 43px - 56px);
		overflow: auto;
		padding: 12px 32px 24px;
		.--tab-content {
			height: 100%;
		}
		.header-container {
			padding: 12px 0 24px;
			position: sticky;
			top: -12px;
			background: $app-white;
			z-index: 1;
			.view-group {
				.btn {
					@include inline-flex-center;
					background: $app-white;
					span {
						padding-left: 8px;
					}
					svg {
						width: 14px;
						height: 14px;
					}
				}
				.btn-selected {
					color: $app-white !important;
					background: $app-primary;
					svg path {
						fill: $app-white;
					}
				}
			}
			.action-group {
				float: right;
			}
			.full-page-link {
				@include inline-flex-center;
				padding: 0 8px;
				margin-right: 0;
				span {
					padding-right: 5px;
				}
				svg {
					width: 12px;
					height: 12px;
				}
			}
		}
		.content-container {
			// overrides for relationship map
			position: relative;
			.relationship-map-wrapper {
				height: calc(100vh - 342px);
				border: 1px solid $border-light-color;
				position: relative;
				.map-section {
					height: 100%;
				}
			}

			.relationship-list {
				.relationship-table {
					width: 100%;
					border-collapse: collapse;
					font-size: $baseFontSize;
					td {
						@include border-style($radius: 0px);
						padding: 18px;
						vertical-align: top;
						cursor: default;
						.relationship-type-name {
							color: $secondary-text-color-1;
						}
						&.relationship-entity-list {
							padding: 8px;
							li {
								@include flexbox-simple(flex-start, center);
								height: 40px;
								cursor: default;
								padding: 4px 0 4px 12px;
								&.has-delete-action:hover {
									background: $background-color-2;
									border-radius: 4px;
									.delete-btn {
										visibility: visible;
										cursor: pointer;
									}
								}
								.entity-type {
									padding-left: 5px;
									color: $secondary-text-color-1;
									font-size: $text-normal;
								}
								.delete-btn {
									margin-left: auto;
									visibility: hidden;
								}
							}
						}
					}
				}
			}

		}

		.relationship-progress-loader {
			position: absolute;
			width: 100%;
			height: 100%;
			@include flex-center;
			z-index: 1;
			background: transparentize($background-color, .7);
			svg {
				animation: spin 1s linear infinite;
			}
		}

		.empty-state-wrapper {
			position: relative;
			top: -100px;
			height: 100%;
			width: 100%;
			@include flex-center;
			@include flex-direction(column);
			margin: auto;
			img {
				width: 150px;
				height: 150px;
			}
			.no-data-desc {
				margin: 20px 0 10px;
				color: $secondary-text-color-1;
				font-size: $title-normal;
			}
		}		
		.add-new-relationship {
			@include inline-flex-center;
			margin-right: 0;
			border: none !important;
			span {
				padding-left: 5px;
				color: $app-primary;
			}
		}
	}

	.create-contract-wrap, .add-user, .add-installations-wrap {
		margin-left: auto;
		svg {
			position: relative;
			width: 14px;
			height: 14px;
			top: 2px;
			path {
				fill: $app-primary;
			}
		}
	}
	.sam-detail-actions {
		margin-right: 8px;
		font-weight: 600;
		border-right: 1px solid $app-border-primary;
		padding-right: 8px;
		position: relative;
	}
	.button-export {
		font-weight: 600;
		line-height: 14px;
		padding: 0;
		border-width: 0;
		&:hover, &:active {
			outline-width: 0;
		}
	}
	svg.highcharts-root {
		height: unset;
		width: unset;
	}
}

.blur-visuals {
	-webkit-filter: blur(4px);
	filter: blur(4px);
	opacity: 0.3;
}

.visual-data-not-available {
	position: absolute;
	top: 40px;
	left: 10%;
	width: 100%;
	text-align: center;
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
	/* IE10+ Specific CSS*/
	.__sam-category-modal {
		.modal__footer {
			position: absolute!important;
		}
	}
	.__sam{
		.grid-container {
			display:block;
			.grid-item{
				width:30%;
				display: inline-block;
			}
			.grid-2-item{
				width: 65%;
				float:right;
			}
		}
	}
}

/* Relationship Modal css */
.relationship-delete-modal {
	color: $primary-text-color-1;
	.ember-modal-header {
		.title {
			font-weight: $text-weight-semi-bold;
		}
	}
}