.sam {
	&.sam-new-layout {
		.page-layout-content{
			flex-wrap: wrap !important;
			background: $app-white;
			padding: 0;
			.page-content{
				padding: 16px 0;
				.content-with-sidebar{
					min-height: 600px;
					display: flex;
					padding-left: 25px;
				}

				.sam-table-wrapper {
					width: 100%;
					padding: 12px 24px 16px 0 !important;
					&.sidebar-open{
						width:calc(100% - 300px)!important;
					}
					.flt-right-flex{
						float: right;
						display: flex;
						.app-add{
							margin:6px 10px 0;
						}
					}
				}
			}
		}
		.page-content-with-sidebar{
			margin: 0;
		}
		.page-content{
			width: 100%;
			margin: 0;
			padding: 0;
		}

		.ember-tooltip{
			max-width: 200px;
			padding: 6px 10px;
			padding-top: 6px;
		}

		.cards-content{
			width: 100%;
			min-height: auto;
			background: $white;
			padding: 20px 12px 0 24px;
			border-radius: 0;
			margin: 0;
			.no-software-wrapper{
				width: 100%;
			}
			.overview-wrap{
				padding-left: 25px;
			}
			.cards-title-wrap{
				display: flex;
				align-items: center;
				justify-content: space-between;
				margin-bottom: 16px;

				&:hover{
					.back-btn {
						border: 1px solid $border-light-color;
					}
				}
				.insights-link{
					display: flex;
					align-items: center;
					font-weight: $text-weight-semi-bold;
					span{
						display: flex;
						padding: 4px;
						svg{
							width: 16px;
							height: 16px;
							fill: $app-primary;
						}
					}
				}
			}

			&.list-view{
				.overview-wrap{
					border-bottom: 1px solid $border-dark-color;
					box-shadow: 0 4px 8px $dropdown-search-shadow-color;
				}
			}

			&.discoverd-view{
				padding-bottom: 10px;
				.page-nav-wrap{
					margin-top:-8px;
				}
				.sam-table-wrapper {
					padding: 16px 24px 16px 0 !important;
				}
				.saas-title{
					padding-bottom: 0;
				}
				.info-text-wrapper{
					margin-bottom: 20px;
					font-size: $baseFontSize;
					color: $app-form-label-color;
				}
			}
		}

		.filter-header,	.ui-list-filter .btns-wrapper{
			background: #f5f7f9!important;
			box-shadow: none;
		}


		.sam-table-options {
			align-items: center;
			margin-top: 16px;
			padding-left: 16px;
			min-height: 17px;
			.sam-add-new-btn-wrap{
				display: flex;
				top: -3px;
				position:relative;
				padding-left: 0;
				svg{
					margin-right: 4px;
					position: relative;
					width: 14px;
					height: 14px;
					top: 2px;
					circle {
						fill: $app-jade-light;
						stroke: $app-green;
					}
					path {
						fill: $app-green;
					}
				}
			}
		}
		.saas-overview-title{
			font-weight: $text-weight-semi-bold;
			font-size: $baseFontSize;
		}
		.saas-title{
			font-weight: $text-weight-semi-bold;
			font-size: $baseFontSize;
			line-height: 20px;
			padding: 0 0 12px;
			display: flex;
			align-items: center;
			color: $inputColor;
			&.big-title{
				font-size: $text-large;
			}
			.back-btn{
				background: $app-white;
				box-shadow: 0 2px 10px $scorecardHoverShadow;
				border: 1px solid $app-white;
				border-radius: 6px;
				padding: 8px;
				width: 32px;
				margin-right: 8px;
				height: 32px;
				fill: $inputColor;
				cursor: pointer;
				svg{
					width: 16px;
					height: 16px;
				}
			}
		}


		.sam-dashboards-cards {
			display: flex;
			justify-content: flex-start;
			.sam-dashboard-card {
				min-width: 200px;
				height: 102px;
				border-radius: 8px!important;
				border: 1px solid $border-dark-color!important;
				box-shadow: 0 1px 6px 0 $dropdown-search-shadow-color;
				padding: 12px 20px 12px 24px;
				background: linear-gradient(0deg, $background-color-2, $background-color-2), $app-white;
				position: relative;
				cursor: default;
				&:hover{
					box-shadow: 0 4px 20px 0 $dropdown-search-shadow-color;
				}
				&:not(:last-child){
					margin-right: 24px;
				}
				&.discovered-card{
					background: $app-white;
					cursor: pointer;
					.sam-card-value{
						&.discovered-card-value{
							color: $linkColor;
						}
					}
				}
				.tooltip-inner{
					text-align: left;
				}

				.sam-card-title{
					font-weight: $text-weight-medium;
					font-family: $baseFontFamily;
					color: $inputColor;
					display: flex;
					justify-content: space-between;
					align-items: center;
					line-height: 0;
					svg{
						fill: $disabled-text-color;
						width: 16px;
						height: 16px;
					}
				}
				.sam-card-value{
					color: $inputColor;
					font-size: $title-extra-large;
					font-weight: $text-weight-semi-bold;
					min-height: 36px;
					padding-top: 20px;
					text-align: left;
					svg{
						fill: $disabled-text-color;
						width: 16px;
						height: 16px;
					}
					&.potentialSavings-card-value{
						color: $app-green;
					}
				}
			}


			&.list-cards{
				box-sizing: border-box;
				padding-bottom: 24px;
			}
			&.discovered-cards{
				.sam-dashboard-card{
					padding: 0 12px 10px 16px;
					height: 72px;
					background: $app-white;
					margin-right: 12px;
					min-width: 180px;
					cursor: pointer;
					.sam-card-value{
						display: flex;
						justify-content: space-between;
						padding-top: 0;
						margin-bottom: 4px;
					}
					&:hover{
						box-shadow: 0 1px 6px 0 $box-shadow-color-1;
					}
				}
			}
		}
		.discovered-cards{
			.sam-dashboard-card {
				&.active{
					border: 1px solid $inputFocusBorder!important;
					// triangle using css
					&:after{
						width: 0;
						content: "";
						display: block;
						border-left: 10px solid transparent;
						border-right: 10px solid transparent;
						border-top: 10px solid $inputFocusBorder;
						position: absolute;
						top: 100%;
						left: 50%;
						margin-left: -10px;
					}
				}
			}
		}
		.set-status-trigger,
		.select-all-btn {
			margin-right: 4px;
			margin-left: 0px;
			display: inline-block;
		}
	}



	.avatar__initial {
		height: 24px;
		width: 24px;
		color: $app-border-primary;
		font-size: $text-normal;
		background: $background-color-2;
		border-radius: 4px;
	}
	.avatar__icon--xsmall {
		border: 1px solid $app-border-primary;
		width: 26px;
		height: 26px;
		min-width: 26px;
		border-radius: 4px;
		margin: 0 7px 0 5px;
	}

	.avatar__icon__img {
		height: auto;
		max-height: 100%;
		margin: auto;
		padding: 2px;
		box-sizing: border-box;
	}

	.table-checkbox {
		position: relative;
	}

	input.sam-search {
		padding: 6px 10px;
		width: 342px;
		margin-left: 24px;
	}

	.sam-status-drop-down-wrapper {
		display: inline-block;
	}
	.sam-drop-down-wrapper {
		margin-left: 10px;
		min-width: 175px;

		.ember-basic-dropdown-content {
			min-width: 150px;
		}
	}

	.sam-table-options {
		padding: 0 0 15px 0;
		min-height: 50px;
	}


	.data-table-link-image {
		display: flex;
		align-items: center;
	}

	.flt-right {
		float: right;
	}

	.filtered-view-banner-wrap {
		padding: 24px 32px 12px;
		width: 100%;
		background-color: $app-white;
		&.discovered-banner {
			padding: 8px 24px 24px 0;
		}
		& + .content-wrap .name-wrap {
			padding-top: 12px !important;
		}
		.filtered-view-banner {
			display: flex;
			border-radius: 4px;
			border: 1px solid $modal-footer-border-color;
			box-shadow: 0 4px 20px 0 $list-item-shadow;
	
			.img-container{
				width: 235px;
				height: 107px;
				img{
					height: 100%;
					border-radius: 7px;
				}
			}
			.text-wrapper {
				width: 73vw;
				padding: 16px 0 4px 0;
	
				.banner-header {
					margin-bottom: 8px;
					font-size: $text-large;
					font-weight: $text-weight-semi-bold;
				}
	
				.help-text {
					color: $secondary-text-color-1;
					padding-bottom: 12px;
					.btn-link{
						padding-left: 0;
						font-family: $baseFontFamily;
						font-weight: $text-weight-semi-bold;
					}
					.second-desc-text {
						margin-top: 8px;
					}
				}
	
				.learn-more-link {
					font-weight: $text-weight-semi-bold;
				}
			}
	
			.img-wrapper {
				.decorative-image {
					height: 140px;
					width: 313px;
					margin-right: 16px;
				}
	
				.close-banner {
					height: 14px;
					width: 14px;
					cursor: pointer;
					vertical-align: top;
				}
			}
	
		}
	}

	.sam-list-table ,.sam-users-table {
		.lt-body-wrap {
			overflow-y: visible;
		}

		.ui-pills-wrapper {
			padding-top: 8px;

			.ui-pills .pill-ellipsis{
				max-width: 60px;
			}
		}

	}

	.sam-list-table {

		th, td {
			box-sizing: border-box;
		}

		.software-list-status, .software-managed-by {
			padding: 0 8px !important;

			.ember-power-select-trigger {
				border-width: 0 !important;
				box-shadow: none;
				background-color: inherit;

				.ember-power-select-status-icon {
					visibility: hidden;
				}

				.ember-power-select-placeholder {
					color: $app-grey;
					font-weight: $text-weight-normal;
				}

				.ember-power-select-selected-item {
					line-height: 20px;
					margin: 8px 0 8px 8px;
					font-weight: normal;
				}
			}

			.ember-basic-dropdown-content {
				width: 300px;

				&.inline-software-status {
					width: 175px;
				}
			}
			.managed-by-wrapper.not-managed, .status-inline-wrapper.no-status {
				.ember-power-select-status-icon {
					visibility: visible !important;
				}
			}

			&:hover {
				.ember-power-select-trigger {
					background: $app-border-primary;
					border-radius: 4px;
				}
			}

			.ember-basic-dropdown-trigger--below {
				background: $app-border-primary;
				border-radius: 4px;

				.ember-power-select-status-icon {
					visibility: visible;
				}
			}
		}

		.lt-row:hover {
			.ember-power-select-trigger {
				.ember-power-select-status-icon {
					visibility: visible;
				}
			}
		}
	}


	.set-status-trigger,
	.select-all-btn {
		margin-right: 12px;
	}
	.sam-list-select-all {
		margin-top: 12px;
		display: inline-block;
	}

	.insights-btn,.app-export{
		margin-right: 16px;
	}

	.insights-btn {

		.insights-icon {
			float: left;
			width: 14px;
			height: 14px;
			margin: 0 4px 0 0;
			fill: $linkColor;
		}
	}


	.assign-category-dd {
		.add-option-item,
		.ember-power-select-option {
			height: 32px;
			display: block;
			line-height: 32px;
			border-radius: 4px;
			margin: 12px 0 0 0;
			position: relative;
			cursor: pointer;
			padding: 0 8px;
			box-sizing: border-box;
		}
		.ember-power-select-option[aria-current="true"],
		.add-option-item:hover {
			background: #f6faff;
		}
	}

	.lt-no-data {
		color: #555555;
		font-size: 14px;
		overflow-wrap: break-word;
		text-align: center;
		background: url("/a/assets/images/empty-states/no-search-results.svg") no-repeat center 21px;
		td {
			padding: 21px;
			text-align: center;
			height: 380px;
			vertical-align: middle;
		}
	}

	.flash-message {
		padding: 0;
		display: inline-block;
	}

	.cat-select-input {
		margin: 6px;
	}

	.install-discovery-wrapper {
		float: left;
		margin: 10% 0 0 28%;
		.no-software-yet {
			font-size: 16px;
			font-weight: 600;
			width: 350px;
		}
		.install-discovery {
			border-top: solid 1px #dad8d8;
			margin: 5% 0 0 0;
			padding-top: 20px;
			width: 300px;
		}
		.install-discovery-btn {
			margin-top: 20px;
		}
	}

	.no-software {
		background: url("/a/assets/images/empty-states/no-software.svg") no-repeat;
		background-position: 15% 75%;
		padding-top: 0;
		background-size: 300px;
		height: 355px;
		width: 40%;
		display: inline-block;
	}

	.access-denied-error {
		width: 100%;
		text-align: center;
		padding-top: 200px;
		.access-denied {
			margin-bottom: 15px;
		}
	}
	.app-filter {
		cursor: pointer;
		margin-left: 16px;
		padding: 7px;
		background: $app-white;
		border: 1px solid $app-border-primary;
		border-radius: 4px;
		height: 32px;
		svg {
			height: 16px;
			width: 16px;
			fill: $app-form-label-color;
		}

		&.active {
			background: $section-background-blue;
			border: 1px solid $app-primary;
			svg{
				fill: $app-primary;
			}
		}
	}
	.app-export{
		background: $app-white;
		border: 1px solid $app-border-primary;
		border-radius: 4px;
		padding: 7px;
		height: 32px;
	}
	.select-all-text{
		top: 1px;
		float: right;
		position: relative;
		padding-left: 8px;
	}
	.dot-loading {
		text-align: center;
	}
}
.sam-insights-modal {

	.ember-modal-header {
		padding-bottom: 4px !important;

		.title {
			font-size: $text-large !important;
			line-height: 20px;
		}
	}

	&.ember-modal-dialog.ember-modal-side .ember-modal-body{
		height: calc(100% - 88px);
	}

	.tabs__content__pane {
		padding: 0;
	}

	.empty-insight {
		margin-top: 100px;

		.empty-text {
			text-align: center;
			margin-bottom: 16px;
			font-size: $text-normal;
			color: $mild-text;
		}

		.empty-insight-img {
			width: 432px;
			height: 194px;
			margin: auto;
			display: block;
		}
	}

	.all-insights{
		margin-top: 18px;
		margin-bottom: 8px;
	}

	.tabs{
		border: none;
		.tab-contents{
			margin-top: 34px;
			.insights-empty-state{
				font-size: $text-normal;
				text-align: center;
				border: 1px solid $border-light-color;
				padding: 16px;
				display: flex;
				flex-direction: column;
				align-items: center;
				border-radius: 4px;
				img{
					width: 315px;
					height: 175px;
					margin: 28px 40px;
				}
			}
		}

		.tabs__items{
			height: 72px;
			display: flex;
			align-items: center;
			width: 70%;
			padding: 0;
			.tabs__item{
				border: 1px solid $app-border-primary;
				box-sizing: border-box;
				border-radius: 8px;
				margin-right: 12px;
				width: 125px;
				height: inherit;
				&.active{
					border: 1px solid $inputFocusBorder;
					&:after{
						width: 0;
						content: "";
						margin: auto;
						display: block;
						position: relative;
						border-left: 10px solid transparent;
						border-right: 10px solid transparent;
						border-top: 10px solid $inputFocusBorder;
						top: -7px;
					}
				}
				&:hover{
					box-shadow: 0 1px 6px 0 $dropdown-search-shadow-color;
				}
			}
			.tabs__item__nav{
				padding: 0 12px 0 10px;
				height: 70px;
				cursor: pointer;
				overflow: visible;
				.tab-title{
					display: flex;
					flex-direction: column-reverse;
					justify-content: flex-end;
					font-size: $text-normal;
					overflow: hidden;
					&:after {
						padding: 0;
						background: none;
						font-size: $title-extra-large;
						height: 35px;
					}
				}
				&.active, &:hover{
					&:after{
						background: none;
					}
				}
			}
		}
	}

	.cards-container {
		padding: 16px;
		min-height: 115px;
		margin-bottom: 16px;
		box-sizing: border-box;
		border: 1px solid $dropdown-hover;
		display: flex;
		justify-content: space-between;
		border-radius: 4px;
		&:hover {
			cursor: pointer;
			box-shadow: 0 1px 6px $list-item-shadow;

			.content .dismiss-btn {
				display: block;
			}
		}

		.insight-icon {
			.logo {
				overflow: hidden;
				border-radius: 2px;
				box-shadow: 0 1px 1px $list-item-shadow;
				border: 1px solid $app-border-primary;

				.avatar__initial {
					font-size: $title-normal;
					color: $app-border-primary;
					background: $background-color-2;
				}

				.avatar__icon__img, .avatar__initial {
					height: 54px;
					width: 54px;
					box-sizing: border-box;
					padding: 2px;
				}
			}
		}

		.content {
			width: 86%;
			word-break: break-word;
			position: relative;
			justify-content: space-between;
			color: $primary-text-color-1;

			.software-card-header{
				display: flex;
				.software-name {
					font-weight: $text-weight-semi-bold;
					color: $linkColor;
					max-width: 290px;
					word-break: break-all;
				}
				.status{
					margin-left: auto;
				}
			}

			.software-category {
				font-size: $text-small;
				line-height: 10px;
				min-height: 10px;
				word-break: break-all;
				margin: -2px 0 8px 0;
			}


			.user-count {
				.value {
					font-weight: bold;
				}
			}

			.savings {
				font-size: $text-normal;
				color: $disabled-text-color;
			}

			.dismiss-btn {
				bottom: 0;
				right: 0;
				margin: 0;
				display: none;
				position: absolute;
			}
		}
	}

	.sub-header-text {
		font-size: $text-normal;
	}
}
