.sam-form-container {

	.flex-wrap{
		display: flex;
		align-items: center;
		overflow: hidden;
	}
	.options-wrap{
		max-height: 300px;
		overflow-y:scroll;
	}

	.autocomplete-field {
		.ember-power-select-dropdown {
			margin-bottom: 8px;
		}
	}

	.loadingIndicator {
		label.preLoadingFields{
			&:after{
				content: ' .';
				font-size: $font-icon-large;
				color: $primary-text-color-2;
				-webkit-animation: dots 1.25s steps(5, end) infinite;
				-moz-animation: dots 1.25s steps(5, end) infinite;
				-o-animation: dots 1.25s steps(5, end) infinite;
				animation: dots 1.25s steps(5, end) infinite;
				line-height: 2px;
			}
			display: inline;
		}
	}

	.add-description {
		font-size: $text-normal;
		font-weight: 600;
		padding: 0;
		border-width: 0;
		&:hover, &:active {
			outline-width: 0;
		}
	}

	.custom-select-option{
		display: flex;
		align-items: center;
	}

	.avatar__initial {
		height: 24px;
		width: 24px;
		color: $app-border-primary;
		font-size: $text-normal;
		background: $background-color-2;
		border-radius: 4px;
	}
	.avatar__icon--xsmall {
		border: 1px solid $app-border-primary;
		width: 26px;
		height: 26px;
		border-radius: 4px;
		margin: 0px 7px 0 5px;
	}

	.avatar__icon__img {
		height: auto;
		margin: auto;
		max-height: 100%;
		padding: 2px;
		box-sizing: border-box;
	}
}

.sam {
	.sam-add-new-btn, .delete-sam-btn {
		margin-right:  0;
	}
}
.sam-details-table-wrap {
	.add-user{
		display: inline;
	}
	.select-all-btn{
		display: block;
		margin: 0 auto 0 16px;
		&.has-selection {
			margin-right: 8px;
		}
	}
}

.add-user-modal, .allocate-license-modal {
	.form-fields {
		.user-license {
			.ember-power-select-option {
				line-height: normal !important;
				height: auto !important;

				&[aria-selected=true]::before {
					top: 25%;
				}
			}
			.user-license-count-wrap {
				.user-prov-license-count {
					padding: 4px 0;
					color: $app-icon-color;
					font-weight: normal;
					font-size: $title-small;
				}
			}

		}
	}
	&:not(.allocate-license-modal){
		.ember-modal-body{
			height: 220px;
		}
	}
}
