.take-action{
	display: inline-block;
}
.sr-form-container{
	.service-request-form{
		padding-bottom: 32px;
	}
	.action-sub-text{
		padding-top: 8px;
	}
	.no-servie-item-empty-state{
		width: 400px;
		min-height: 300px;
		text-align: center;
		margin: 80px auto;

		.create-service-item-btn{
			display: block;
			margin: 0 auto;
		}
	}
	.succes-card-wrapper{
		border: 1px solid $inputDisabledBorder;
		margin-bottom: 16px;
		.success-sr-card {
			border: hidden!important;
			border-radius: 0!important;
			padding: 4px 24px!important;
			&:last-child{
				margin-bottom: 1px!important;
			}
			.selected-users{
				width: 70%!important;
			}

		}
		.success-header-card{
			background: $background-color-2;
			border: hidden!important;
			box-shadow: none;
			border-radius: 0;
			padding: 4px 16px;
		}
	}

	.selected-user-card{
		border: 1px solid $inputDisabledBorder;
		margin-bottom: 16px;
		height: 82px;
		&.in-progress-card,&.failure-card{
			padding: 4px 16px;
		}
		.selected-user-wrap{
			display: flex;
			align-items: center;
			.software-logo{
				width: 50px;
				height: 50px;
				border: 1px solid $border-light-color-2;
				border-radius: 4px;
				margin-right: 20px;
				img{
					max-width: 100%;
					height: 100%;
					padding: 4px;
					box-sizing: border-box;
				}
			}
			.logo-img{
				margin-right: 20px;
				width: 6%;
				svg{
					width: 30px;
					height: 30px;
					&.success{ fill: $app-green; }
					&.failure{ fill: $btnDangerBorder; }
					&.partially_successful{ fill: $app-warning-state; }
					&.loading { fill:$inputFocusBorder; }
				}
				img{
					width: 50px;
					height: 50px;
				}
			}
			.selected-users{
				font-weight: $text-weight-semi-bold;
				font-size: $baseFontSize;
				line-height: 20px;
				.selected-user-count{
					padding-top:2px;
					font-weight: $text-weight-semi-bold;
					font-size:$text-large;
					line-height: 24px;
				}
				.sr-item {
					color: $app-primary;
					font-weight: $text-weight-semi-bold;
					overflow: hidden;
					text-overflow: ellipsis;
					white-space: nowrap;
					padding-bottom: 2px;
					span:first-child {
						color: $secondary-text-color-2;
						padding-right: 2px;
					}
					&.link:hover{
						cursor: pointer;
					}
				}
				.sr-count{
					font-size: $baseFontSize;
					font-weight: normal;
				}
				.sr-count-number{
					padding-right: 10px;
					color:yellow!important;
				}
				.loading-txt{
					padding-top: 2px;
					font-size: $text-normal;
					font-weight: $text-weight-normal;
				}
				.partial-txt{
					color: $app-error-state;
				}
				.failure-txt{
					color: $btnDangerBorder;
				}
			}
			.sr-indicator{
				margin-left: auto;
			}

			.sr-props {
				border-left: 1px solid $border-light-color;
				padding-left: 10px;
				.sr-props-item {
					padding: 6px 0;
					display: flex;
					.label-txt {padding-right: 30px;}
					.priority-indicator-wrap{
						display: flex;
						align-items: center;
						.priority-indicator{
							width: 10px;
							height: 10px;
							background: $app-green;
							border-radius: 2px;
							margin-right: 4px;
						}
					}
				}

			}
			.status-pill{
				padding: 20px 0;
				display: flex;
				align-items: center;
				min-width: 120px;
				justify-content: center;
				.state{
					border-radius: 4px;
					padding: 4px 8px;
					margin-left: 8px;
					font-weight: $text-weight-semi-bold;
					font-size: $text-normal;

					&.loading{
						border: 1px solid $border-light-blue;
						background-color: $section-background-blue;
						color: $app-primary;
					}
					&.failure{
						border: 1px solid $alertWarningBorder;
						background-color: $alertWarningBackground;
						color: $app-red-1;
					}
					&.success{
						border: 1px solid $alertSuccessBorder;
						background-color: $alertSuccessBackground;
						color: $alertSuccessColor;
					}
					&.partially_successful{
						display: inline-block;
						min-width: 120px;
						border: 1px solid $alertPartialBorder;
						background-color: $app-casablanca-light;
						color: $alertPartialColor;
					}
				}
				.try-again-btn{
					background: transparent;
					border: 2px solid $inputFocusBorder;
					padding: 6px 12px;
					min-width: 110px;
					color: $inputFocusBorder;
					font-size: $baseFontSize;
				}
			}
		}
	}
}
