.relationship-map-wrapper {
	font-family: $baseFontFamily;
	font-size: $baseFontSize;
	height: 100vh;

	.rel-map-header {
		display: flex;
		min-height: 64px;
		align-items: center;
		padding: 0 24px;
		border: 1px solid $border-light-color;
		background: $white;

		.text-wrapper {
			max-width: calc(100% - 200px);
			overflow: hidden;
			word-break: break-word;
			padding: 16px 0;

			.root-type {
				display: inline-block;
				font-size: $title-normal;
				font-weight: $text-weight-semi-bold;
				padding-right: 12px;
				text-transform: capitalize;
			}

			.root-name {
				text-transform: capitalize;
			}
		}

		.map-settings {
			margin-left: auto;

			.settings-icon {
				width: 16px;
				height: 16px;
				top: 3px;
				position: relative;
			}

			.map-settings-btn {
				margin-right: 0;
			}

			.map-level-choices {
				min-width: 120px;
				white-space: nowrap;
				padding: 8px;
				.basic-dropdown-item{
					padding: 6px 20px!important;
				}
			}
			.ember-basic-dropdown-content{
				padding: 0;
				min-width: 120px;
			}
		}
	}


	.map-setting-options {
		width: 300px;
		.level-swtich-wrap {
			display: flex;
			justify-content: space-between;
			.map-level-switch {
				.ember-basic-dropdown-trigger {
					font-weight: $text-weight-semi-bold;
				}
			}
		}
		.basic-dropdown-item {
			padding: 16px 20px !important;
		}

		li.basic-dropdown-item span:nth-child(2) {
			float: right;
			font-weight: $text-weight-semi-bold;
		}

	}

	.map-alert {
		margin: 8px !important;
		width: calc(100% - 16px);
		position: absolute;
		.close-btn {
			cursor: pointer;
			margin-left: auto;
			top: 3px;
			position: relative;
			svg {
				margin-right: unset;
			}
		}
		svg path {
			fill: $secondary-text-color-1;
		}
			
	}

	.map-actions {
		display: flex;
		flex-direction: column;
		position: absolute;
		top: 47%;
		left: 24px;

		.zoom-btn {
			width: 32px;
			padding: 0;
			margin-bottom: 8px;
			line-height: 10px;

			svg {
				height: 14px;
				width: 14px;
			}

			&.reset-btn svg {
				height: 20px;
				width: 20px;
			}
		}
	}

	.map-section {
		background: $app-white;
		height: calc(100% - 64px);
		overflow: hidden;

		.map-props{
			position: absolute;
			.ui-pills-wrapper{
				padding: 16px;
				.ui-pills{
					border: 1px solid $app-border-primary;
					font-weight: $text-weight-semi-bold;
					padding: 1px 10px 0px;
					min-height: 19px;
				}
			}
		}

		.relationship-map-svg {
			width: 100% !important;
			height: 100% !important;
			overflow: hidden;
			outline: 0;

			#gradient-start {
				stop-color: $app-white;
				stop-opacity: 1;
			}

			#gradient-stop {
				stop-color: $app-sidebar-bg;
				stop-opacity: 1;
			}

			.node-wrapper {
				font-size: $text-normal;
				fill: $app-grey;
				outline: 0;
				text-transform: capitalize;

				.node-box {
					stroke: $app-border-primary;
					fill: url("#node-icon-gradient");
				}

				.node-hover-box {
					fill: $background-color;
					height: 44px;
					display: none;

					&.node-hovered {
						display: block;
					}
				}

				&:not(.root-node) .node-hover-box {
					width: 150px;
				}

				.node-type {
					font-weight: $text-weight-medium;
				}

				.active {
					stroke: $highlight-blue!important;
				}

				.incident-count {
					font-size: 8px;
					border-radius: 8px;
					color: $app-white;
					font-weight: $text-weight-semi-bold;
					background: $checkboxIndeterminateBackground;
					padding: 1px 6px;
					float: right;
				}

				&.node-dragged {
					cursor: grabbing;
				}

				&.root-node {
					.node-box {
						stroke: $inputFocusBorder;
					}

					.incident-count {
						background: $app-primary;
					}

					.root-node-details, .node-name {
						fill: $inputFocusBorder;
					}
				}

				&.gray-out {
					.node-name, .node-type, .node-icon, .root-node-details {
						fill: $mild-text;
					}
					.node-box {
						fill: $background-color;
						stroke: $mild-text;
						filter: none;
					}
				}
			}

			.map-links {
				fill: $app-grey;
				stroke-width: 1px;
				.map-link{
					cursor: pointer;
					fill: $app-grey;
					stroke-width: 1px;
					stroke: $app-icon-color;
				}
				.link-text {
					font-size: $text-small;
				}

				.link-arrow-head {
					font-size: 32px;
				}

				.link-arrow-tail {
					font-size: 40px;
				}

				.active{
					stroke: $highlight-blue;
					.link-arrow-head,.link-arrow-tail{
						fill:  $highlight-blue;
					}
				}
				.transparent-link{
					stroke: transparent;
					stroke-width: 4;
				}
			}
		}

		.map-tooltip{
			opacity: 0;
			display: flex;
			justify-content: center;
			align-items: center;
			flex-wrap: wrap;
			position: fixed;
			text-align: center;
			padding: 10px;
			text-indent: 4px;
			pointer-events: none;
			background: $border-light-color;
			box-shadow: 0px 2px 2px $app-footer-border-bottom;
			border-radius: 4px;
			transition: opacity 0.7s;
			margin: 20px 15px;
			span{
				text-transform: capitalize;
			}
			strong{
				font-weight: $text-weight-semi-bold;
			}
			&.tooltip-active{
				opacity: 1;
				transition: opacity 0.2s;
			}
		}
		.node-hover-actions {
			position: absolute;
			box-shadow: 0px 4px 8px $app-footer-border-bottom;
			border: 1px solid $border-dark-color;
			max-width: 250px;
			border-radius: 8px;
			overflow: hidden;
			display: none;
			font-size: $text-normal;

			.node-details {
				padding: 8px 12px;
				background: $background-color-2;
				text-transform: capitalize;
				color: $app-form-label-color;
				max-width: 250px;
				div:first-child {
					font-weight: $text-weight-medium;
				}

				.open-incidents {
					display: flex;
					width: max-content;
					padding: 0 10px 0 6px;
					margin-top: 6px;
					border-radius: 8px;
					background: $app-white;
					font-size: $text-small;
					font-weight: $text-weight-semi-bold;

					.open-incidents-icon {
						height: 16px;
						width: 16px;
					}

					.open-incidents-text {
						line-height: 16px;
						padding-left: 3px;
					}
				}
			}

			.node-actions-wrapper {
				padding: 8px;
				background: $app-white;

				.node-action {
					padding: 4px 8px;
					border-radius: 4px;
					cursor: pointer;
					border: 0 none;
					text-align: left;
					font-family: $baseFontFamily;
					font-size: $text-normal;
					background: $app-white;
					line-height: 16px;
					width: 100%;

					&:not(:last-of-type){
						margin-bottom: 8px;
					}
					&:hover {
						color: $primary-text-color-1;
						background: $section-background-blue;
					}

					&:active {
						box-shadow: inset 0 0 4px 0 $app-footer-border-bottom;
					}
				}
			}

			&.active, &:hover {
				display: block;
			}
		}
		.loader-container {
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
		}
	}
}
.relationship-add-modal {
	font-family: $baseFontFamily;
	font-size: $baseFontSize;

	.ember-modal-body {
		overflow-x: hidden;
	}

	.title {
		font-size: $text-large!important;
		margin: 0;
		font-weight: $text-weight-medium;
	}

	.current-node-details {
		font-weight: $text-weight-semi-bold;
		font-size: $text-large;
		color: $secondary-text-color-1;
		margin-top: 12px;
		text-transform: capitalize;

		.current-node-type {
			font-size: $baseFontSize;
			font-weight: $text-weight-normal;
			padding-left: 4px;
		}
	}

	.pagination-wrapper {
		nav li {
			list-style: none;
		}
	}

	.add-relationship-table-options {
		padding-top: 8px;
		box-sizing: border-box;

		&::after {
			clear: both;
			display: block;
			content: "";
			height: 0;
		}

		#add-relationship-select-all {
			padding: 8px 0 0 16px;
			display: block;
			font-size: $text-normal;
			float: left;
			color: $app-form-label-color;

			label {
				vertical-align: bottom;
			}
		}
	}

	// add side pane table css changes
	.add-relationship-table {
		tr,th,td,.ember-light-table {
			border: none !important;

			.lt-head.lt-inline {
				position: sticky;
				position: -webkit-sticky;
				top: 0;
				z-index: 1;
			}

			.lt-body-wrap {
				overflow: visible;

				thead.lt-head th.lt-column {
					height: 36px;
				}

				>table {
					margin: 8px 0 16px 0;
				}

				.ember-power-select-placeholder {
					color: $primary-text-color-1;
				}

				.check-box-cell {
					box-sizing: border-box;
				}

				.lt-no-data {
					background-position-y: center;
					td {
						height: 350px;
						vertical-align: bottom;
						color: $secondary-text-color-1;
						font-size: $title-normal;
						.no-records-txt {
							margin: 20px 0 10px;
						}
					}
				}
			}
		}
		.empty-state-container {
			top: 100px;
		}
	}

	// node type dropdown style
	.node-filters {
		margin-bottom: 16px;

		.node-types {
			width: 100%;
			margin-bottom: 0;
			@include flexbox-simple(space-between, center);

			label {
				padding-bottom: 4px;
			}

			.ember-basic-dropdown {
				height: 32px;

				.ember-basic-dropdown-content {
					z-index: 2;
				}
			}

			.ember-power-select-option {
				line-height: 20px;
			}

			.select-entityType, .select-sub-entityType {
				width: 48%;
				margin: 0;
			}
		}

		.relationship-node-search-wrapper {
			position: relative;
			width: 100%;
			margin: 16px 0;

			.search-container {
				position: absolute;
				top: 8px;
				left: 8px;			

				svg {
					height: 16px;
					width: 16px;
					fill: $app-form-label-color;
				}
			}

			.relationship-node-search {
				width: 100%;
				padding-left: 32px;
			}
		}
	}

	.relationship-type-wrapper, .node-filters {

		// hide default search clear button, already hidden in other ember pages due to legacy css
		input[type="search"]::-webkit-search-cancel-button, input[type="search"]::-webkit-search-decoration {
			-webkit-appearance: none;
		}
	}

}
.relationship-details-modal{
	.ember-modal-header{
		padding-bottom: 24px;
	}
	.current-node-details{
		margin-top: 0px;
	}
	.title {
		font-weight: $text-weight-semi-bold;
		font-size: $text-large !important;
		color: $app-primary;
		cursor: pointer;
		width: 400px;
		display: block !important;
	}
	.details-title{
		color: $app-form-label-color;
		padding-bottom: 16px;
		font-weight: $text-weight-normal;
	}

	.current-node-header-wrap{
		display: flex;
		justify-content: space-between;
		.current-node-type{
			width: 400px;
			text-transform: capitalize;
			padding-left: 0px!important;
			font-weight: $text-weight-medium;
		}
		.add-relationship-btn {
			padding: 4px 8px;
			border-radius: 4px;
			cursor: pointer;
			white-space: nowrap;
			font-family: $baseFontFamily;
			font-size: $text-normal;
			line-height: 16px;
			margin-right: 0px;
		}
	}

	.node-details-section{
		border: 1px solid $border-dark-color;
		border-radius: 4px;
		>div{
			padding: 15px;
		}
	}
	.row-border{
		border-bottom: 1px solid $border-dark-color;
		.data-row{
			padding-bottom: 12px;
		}
	}
	.row-2-column {
		display: flex;
		justify-content: flex-start;
		span{
			width: 240px;
		}
		>div{
			flex-grow: 1;
			flex-basis: 0;
		}
		.data-column{
			width: 25%;
			padding-left: 10px;
			&:not(:last-child){
				border-right: 1px solid $border-dark-color;
			}
		}
	}
	.row{
		padding: 10px;
		.avatar-col{
			width: 156px;
			margin-right: 16px;
			flex-grow: 0.5;
			.avatar__icon {
				.avatar__initial{
					font-size: 56px;
					font-weight: $text-weight-normal;
					width: 140px;
					height: 140px;
				}
			}
		}
		span {
			display: block;
		}
		.property-title{
			padding-bottom: 6px;
			font-size: $text-normal;
			color: $app-form-label-color;
		}
		.value-bold{
			font-weight: $text-weight-semi-bold;
			font-size: $title-normal;
			margin-bottom: 8px;
			&.link:hover{
				cursor: pointer;
				color: $linkColor;
			}
		}
		.capitalize-value{
			text-transform: capitalize;
		}
	}
	hr{
		width: calc(100% + 62px);
		margin-left: -32px;
		margin-top: 20px;
		border: 0;
		border-bottom: 2px solid $background-color-2;
	}

	.unresolved-issues-wrapper {
		color: $app-form-label-color;

		.details {
			display: flex;
			justify-content: space-between;
			margin-top: 24px;

			.tab-label {
				.from-date {
					font-size: $text-normal;
					padding-left: 4px;
				}
			}

			.see-all-issues {
				font-weight: $text-weight-semi-bold;
			}
		}

		.no-decoration {
			text-decoration: none;
		}

		.empty-wrapper {
			display: flex;
			flex-direction: column;
			align-items: center;
			margin-top: 140px;

			img {
				width: 140px;
				height: 140px;
			}

			.empty-text {
				padding-top: 16px;
			}
		}

		.unresolved-issue-tabs {
			padding-top: 12px;

			.tabs__content__pane {
				padding: 4px 0;

				.issue-label {
					padding: 4px 0;
					font-size: $text-normal;
				}

				.cards-wrapper {
					height: calc(100vh - 360px);
					position: absolute;
					overflow: auto;
					width: calc(100% - 64px);
				}

				.cards-container {
					border: 1px solid $border-light-color;
					box-sizing: border-box;
					box-shadow: 0px 1px 6px $box-shadow-color-1;
					border-radius: 4px;
					margin: 8px 0 16px 0;
					padding: 12px 24px;
					display: flex;
					justify-content: space-between;
					line-height: 20px;

					.details-wrapper {
						word-break: break-word;
						overflow: hidden;
						word-wrap: break-word;

						.issue-subject, .user-name, .created, .start-date-value, .end-date-value {
							font-weight: $text-weight-semi-bold;
						}

						.from-details {
							display: flex;
							.from-label {
								word-break: keep-all;
							}
						}
	
						.user-name {
							color: $app-form-label-color;
							padding-left: 4px;
							&:hover {
								color: $linkColor;
							}
						}
					}

					.badge-wrapper {
						padding: 12px 0 0 16px;

						.ember-badge {
							max-width: 200px;
							word-break: break-word;
						}
					}
				}

				.view-more-btn {
					font-size: $text-normal;
					font-weight: $text-weight-semi-bold;
					margin-top: -4px;
				}
			}

			// tab nav css
			.tabs__items {
				height: 72px;
				padding: 0;

				.tabs__item {
					border: 1px solid $app-border-primary;
					box-sizing: border-box;
					border-radius: 8px;
					margin-right: 12px;
					width: 108px;
					height: inherit;

					&.active{
						border: 1px solid $inputFocusBorder;
						// triangle using css
						&:after{
							width: 0;
							content: "";
							margin: auto;
							display: block;
							position: relative;
							border-left: 10px solid transparent;
							border-right: 10px solid transparent;
							border-top: 10px solid $inputFocusBorder;
							top: -6px;
						}	
					}

					&:hover{
						box-shadow: 0px 1px 6px 0px $box-shadow-color-1;
					}
				}

				.tabs__item__nav {
					padding: 0 10px;
					height: 70px;
					cursor: pointer;
					overflow: visible;

					.tab-title {
						display: flex;
						flex-direction: column-reverse;
						justify-content: flex-end;
						font-size: $text-normal;
						padding-left: 24px;
						overflow: hidden;

						&:after {
							padding: 0;
							background: none;
							font-size: $title-extra-large;
							font-weight: $text-weight-semi-bold;
							height: 34px;
							position: relative;
							right: 22px;
						}
					}

					.app-icon {
						position: absolute;
						top: 44px;
						fill: $app-form-label-color;
						height: 20px;
						width: 20px;
					}

					&.active, &:hover{
						color: $app-form-label-color;
						&:after{
							background: none;
						}
					}
				}
			}
		}
	}


	.sw-detail {
		.sw-header-wrap {
			@include flexbox-simple(flex-start, center);
			.sw-img {
				margin-right: 10px;
				@include border-style($border-light-color-2, 4px);
				padding: 4px;
				img {
					width: 35px;
				}
			}
			.sw-title {
				font-size: $title-large;
				font-weight: $text-weight-semi-bold;
				padding-bottom: 5px;
				margin: 0;
			}
			.sw-category{
				font-size: $text-normal;
				color: $app-form-label-color;
				margin: auto;
				span:not(.property_title){
					font-weight: $text-weight-medium;
					padding: 0 5px;
					word-break: break-all;
					&.sw-status{
						text-transform: capitalize;
					}
				}
			}
		}
		.sw-desc {
			word-break: break-all;
			margin-top: 10px;
			font-size: $text-normal;
			.btn-link {
				font-size: 12px;
				padding-left: 0;
			}
		}
	}
	.sw-stat {
		border-top: 1px $app-border-primary solid;
		border-bottom: 1px $app-border-primary solid;
		padding: 0 5px !important;
		margin: 0;

		.data-column {
			padding: 12px !important;
		}
	}
	.sw-note {
		font-size: $text-normal;
		margin: auto;
	}
}
